<template>
  <div class="autumnMenu">
    <el-card class="box-card">
      <div class="box1">
        <h1>{{content[0].menuName}}</h1>
        <img :src="content[0].pic" />
      </div>
      <div class="box2">
        <h2>用料:</h2>
        <h3>{{content[0].menuMaterial}}</h3>
        <h2>做法:</h2>
        <h3>
          <ul v-for="(item,index) in step" :key="index">
            <li>{{item}}</li>
          </ul>
        </h3>
      </div>
      <el-divider>
        <i class="el-icon-eleme"></i>
      </el-divider>
      <el-card>
        <div class="discuss" v-if="show">
          <b>用户评论 |</b>
          <ul v-for="(item,index) in comment" :key="index">
            <li class="discuss_list">
              <div class="user_discuss">
                <span class="user_name">{{item.name}}</span>:
                <span>{{item.comment}}</span>
              </div>
              <div class="el_but">
                <el-button
                  icon="el-icon-close"
                  type="danger"
                  size="mini"
                  circle
                  @click="deleteComment(item.commentId)"
                ></el-button>
              </div>
            </li>
            <el-divider></el-divider>
          </ul>
        </div>
        <div class="discuss" v-else>
          <b>用户评论 |</b>
          <h2>暂无评论！</h2>
          <el-divider></el-divider>
        </div>
      </el-card>
      <!-- <div>
      <ul v-for="(item,index) in comment" :key='index'>
        <li><span>{{item.name}}</span>:<span>{{item.comment}}</span><button @click="deleteComment(item.commentId)">删除评论</button><hr></li>
      </ul>
      </div>-->
      <p class="detailBottom">
        <button
          @click="collectBtn(content[0].menuId)"
          v-if="$store.state.autumnCollectId.indexOf(parseInt(content[0].menuId))===-1"
        >收藏</button>
        <button @click="cancelCollectBtn(content[0].menuId)" v-else>取消收藏</button>
        <router-link tag="button" :to="{path:'/autumncomment',query:{id:content[0].menuId}}">评论</router-link>
        <button v-if="isUser" @click="deleteMenu">删除菜谱</button>
        <router-link
          v-if="isUser"
          tag="button"
          :to="{path:'/modifyautumn',query:{id:content[0].menuId}}"
        >修改菜谱</router-link>
        <router-link tag="button" to="seasonalmenu/autumnpage">返回</router-link>
      </p>
    </el-card>
  </div>
</template>

<script>
import { request } from "../../../../network/request";
export default {
  name: "AutumnMenuContent",
  data() {
    return {
      comment: [],
      content: [],
      step: [],
      obj: {
        menuId: this.$route.query.id,
        pic: ""
      },
      isUser: false,
      show: false
    };
  },
  methods: {
    collectBtn(menuId) {
      if (localStorage.getItem("token")) {
        this.$store.commit("addAutumnCollectId", menuId);
        const obj = {
          menuId: menuId
        };
        const jon = JSON.stringify(obj);
        request({
          url: "/autumnCollect",
          method: "post",
          data: jon,
          headers: { "Content-Type": "application/json;charset=UTF-8" }
        })
          .then(res => {
            console.log(res);
            alert("收藏成功！");
          })
          .catch(err => {
            alert("您已经收藏该菜谱了！");
            console.log(err);
          });
      } else {
        this.$router.push("/login");
      }
    },
    cancelCollectBtn(menuId) {
      //取消收藏菜谱，从菜谱数组中删除
      Array.prototype.remove = function(val) {
        for (var i = 0; i < this.length; i++) {
          if (this[i] == val) this.splice(i, 1);
        }
      };
      this.$store.state.autumnCollectId.remove(parseInt(menuId));
      const obj = {
        menuId: menuId
      };
      const jon = JSON.stringify(obj);
      request({
        url: "/cancelAutumnCollect",
        method: "post",
        data: jon,
        headers: { "Content-Type": "application/json;charset=UTF-8" }
      })
        .then(res => {
          console.log(res);
          alert("该菜谱已取消收藏！");
        })
        .catch(err => {
          console.log(err);
        });
    },
    deleteComment(commentId) {
      const obj = {
        commentId: commentId
      };
      const jon = JSON.stringify(obj);
      request({
        url: "/deleteAutumnComment",
        method: "post",
        data: jon,
        headers: { "Content-Type": "application/json;charset=UTF-8" }
      })
        .then(res => {
          console.log(res);
          alert("删除评论成功！");
          location.reload();
        })
        .catch(err => {
          console.log(err);
        });
    },
    deleteMenu() {
      const jon = JSON.stringify(this.obj);
      request({
        url: "/deleteAutumn",
        method: "post",
        data: jon,
        headers: { "Content-Type": "application/json;charset=UTF-8" }
      })
        .then(res => {
          console.log(res);
          alert("该菜谱已经删除成功了");
          this.$router.push("/seasonalmenu/autumnpage");
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  created: function() {
    const jon = JSON.stringify(this.obj);
    request({
      url: "/autumnContentShow",
      method: "post",
      data: jon,
      headers: { "Content-Type": "application/json;charset=UTF-8" }
    })
      .then(res => {
        console.log(res);
        this.content = res.data.message;
        this.step = this.content[0].menuContent.split("。");
      })
      .catch(err => {
        console.log(err);
      });

    request({
      url: "/existAutumnUser",
      method: "post",
      data: jon,
      headers: { "Content-Type": "application/json;charset=UTF-8" }
    })
      .then(res => {
        this.isUser = res.data.isUser;
        this.obj.pic = res.data.message[0].pic;
      })
      .catch(err => {
        console.log(err);
      });

    request({
      url: "/autumnCommentShow",
      method: "post",
      data: jon,
      headers: { "Content-Type": "application/json;charset=UTF-8" }
    })
      .then(res => {
        console.log(res.data.message);
        if (res.data.message) {
          this.comment = res.data.message;
          this.show = true;
        } else {
          this.show = false;
        }
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>

<style scoped>
.box1 {
  text-align: center;
}
.box2 {
  padding: 0 250px;
}
.box2 ul {
  list-style: none;
}
p.detailBottom {
  text-align: center;
  margin: 15px auto;
}
p.detailBottom a {
  display: inline-block;
  min-width: 15px;
  min-height: 15px;
  border: 1px solid #dddddd;
  padding: 5px;
  margin: 10px;
  border-radius: 5px;
  background: #e0f1ee;
  text-decoration: none;
  color: #66666a;
}
p.detailBottom button {
  display: inline-block;
  margin: 10px;
  border: 2px solid #f6652c;
  background-color: #f6652c;
  height: 30px;
  width: 60px;
  color: #fff;
  font-size: 14px;
  border-radius: 5px;
}
img {
  width: 400px;
  height: 266px;
}
.box-card {
  width: 980px;
}
.autumnMenu {
  min-width: 1280px;
  display: flex;
  justify-content: center;
}
.box-card {
  width: 980px;
}
.MenuContent {
  min-width: 1280px;
  display: flex;
  justify-content: center;
}
.discuss {
  padding: 154px;
}
.discuss > ul {
  margin: 20px;
  list-style: none;
  font-family: "Times New Roman", Times, serif;
}
.discuss_list {
  display: flex;
}
.el_but {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}
.user_name {
  font-family: cursive;
  padding: 3px;
  font-weight: 600;
}
</style>